<template>
    <div class="nav-bar">
        <router-link to="/products" class="products-link">
            <div class="logo-wrap">
                <img :src="logo">
            </div>
        </router-link>
        <div class="nav-buttons-wrap">
            <button v-if="user" @click="signOut">Sign out</button>
            <router-link to="/cart" >
                <button>Shopping Cart</button>
            </router-link>
        </div>
    </div>
</template>

<script>

import { getAuth, signOut } from 'firebase/auth';

export default {
    name: "NavBar",
    props: ["user"],
    data() {
        return {
            logo: '',
        }
    },
    methods: {
        signOut() {
            const auth = getAuth();
            signOut(auth)
        }
    },
    async created() {
        this.logo = '/images/logo-hexagon.svg'
    }
}
</script>